
.pre_junior {
    text-decoration: none;
    color: red;
    margin: 10px;
}

.pre_junior:hover {
    color: greenyellow;
}

.pre_junior:active {
    color: blue;
}

.block {
    border-radius: 6px;
    width: 25px;
    height: 25px;
    margin: 25px;
    background: #99ff99;
}

.navigation {
    color: cyan;
}

.navigation:active {
    color: coral;
}


.header {
    display: flex;
    width: 620px;
    transition-duration: 3000ms;
    transform: translate(-500px);
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-evenly;
    align-content: flex-end;
    align-items: baseline;
}

.header:hover {
    transition-duration: 1000ms;
    transform: translate(0px);
}

