.rotate {
    width: 25px;
    height: 25px;
    animation: rotation infinite 3s linear;


}

@keyframes rotation {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}

.click {
    margin-right: 30px;
    color: white;
}
